<template>
  <div class="question question--maptype">
    <div class="maptype__wrapper">
      <lMap
        :zoom="zoom"
        :center="center"
        ref="myMap"
        @ready="handlerReadyMap()"
        @click="handlerClikMap"
      >
        <LTileLayer :url="url" :attribution="attribution"></LTileLayer>
        <lMarker
          v-for="(markerLatLng, index) of markerLatLngs"
          :key="index"
          :lat-lng="markerLatLng"
          @click.stop
        >
          <LTooltip :options="{ direction: 'bottom', offset: [-14, 30] }">
            Tooltip
          </LTooltip>
        </lMarker>
      </lMap>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import "leaflet/dist/leaflet.css";
import { LMap, LTileLayer, LMarker, LTooltip, LPopup } from "vue2-leaflet";
import { Icon } from "leaflet";

delete Icon.Default.prototype._getIconUrl;

Icon.Default.mergeOptions({
  iconRetinaUrl: require("leaflet/dist/images/marker-icon-2x.png"),
  iconUrl: require("leaflet/dist/images/marker-icon.png"),
  shadowUrl: require("leaflet/dist/images/marker-shadow.png"),
});

export default {
  components: {
    LMap,
    LTileLayer,
    LMarker,
    LTooltip,
    LPopup,
  },

  data() {
    return {
      map: null,
      url: "https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png",
      attribution:
        '&copy; <a target="_blank" href="http://osm.org/copyright">OpenStreetMap</a> contributors',
      zoom: 15,
      center: [51.505, -0.159],
      markerLatLngs: [],
    };
  },
  methods: {
    handlerClikMap(event) {
      this.markerLatLngs.push([event.latlng.lat, event.latlng.lng]);
    },
    handlerReadyMap() {
      this.map = this.$refs.myMap.mapObject;
    },
  },
  created() {
    navigator.geolocation.getCurrentPosition((success) => {
      const {coords } = success
      const { latitude, longitude } = coords
      const position = [latitude, longitude]
      this.center = position
      this.markerLatLngs.push(position)
      console.log(position)
    }, (error) => {
      const { message } = error
      console.error(message)
    }, {
      enableHighAccuracy: true
    })
  }
};
</script>

<style scoped>
#map {
  height: 100%;
}
</style>
